.Home-container {
    text-align: center;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 50px 50px;
    background-color: white !important;
    max-width: 980px;
}

.Home-content-name {
    margin-left: auto;
    margin-right: auto;
}

.Home-header{
    background-size: cover;
    background-color: white;
    height: 80px; 
    margin-left: auto;
    margin-right: auto;
}

.home-header-text{
    font-size: 80px;
    margin-left: auto;
    margin-right: auto;
    grid-area: home-header-text;
    max-width: 980px;
}

.Home-nav-buttons{
    background-image: 
        linear-gradient(
            to bottom, 
            #ffffff 0%, 
            #0f059e5e 100%
            );    
    color: rgb(22, 21, 21);
    cursor: pointer;
    height: 45px;
    width: 65px;
    margin: -30px 0 0 600px;
    border: none;
    font-style: italic;
    font-size: 25px;
    grid-area: buttons;
    padding: 0 !important;
    z-index: 0;
    position: relative; 
}

.Home-nav-buttons:hover {
    color: rgba(255, 0, 0, 0.356);
}


.Home-nav-buttons span {
	align-items: center;
	background: white;
	display: inline-block;
	height: 100%;
	transition: background .5s ease;
	width: 100%;
}

.Home-nav-buttons:hover span {
	background: transparent;
} 

.Home-nav-buttons-after{
    background-image: 
        linear-gradient(
            to bottom, 
            #ffffff 0%, 
            #0f059e5e 100%
            );    
    color: rgb(22, 21, 21);
    cursor: pointer;
    height: 45px;
    width: 100px;
    margin-top: -30px;
    border: none;
    font-style: italic;
    font-size: 25px;
    padding: 0 !important;
    z-index: 0;
    position: relative; 
}

.Home-nav-buttons-after:hover {
    color: rgba(255, 0, 0, 0.356);
}


.Home-nav-buttons-after span {
	align-items: center;
	background: white;
	display: inline-block;
	height: 100%;
	transition: background .5s ease;
	width: 100%;
}

.Home-nav-buttons-after:hover span {
	background: transparent;
} 

